import { getCameraById } from "@/lib/data";
import { notFound } from "next/navigation";
import Image from "next/image";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import FavoriteButton from "@/components/favorite-button";
import { Map, AdvancedMarker, Pin } from "@vis.gl/react-google-maps";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { ArrowLeft } from "lucide-react";
import { Skeleton } from "@/components/ui/skeleton";

type Props = {
    params: { id: string };
};

export async function generateMetadata({ params }: Props) {
    const camera = getCameraById(params.id);
    if (!camera) {
        return { title: 'Camera Not Found' };
    }
    return {
        title: `${camera.name} | Kiwi Traffic Watch`,
        description: camera.description,
    };
}


export default function CameraPage({ params }: Props) {
    const camera = getCameraById(params.id);

    if (!camera) {
        notFound();
    }

    return (
        <div className="container mx-auto max-w-5xl py-8 px-4">
            <Button asChild variant="ghost" className="mb-6">
                <Link href="/">
                    <ArrowLeft className="mr-2 h-4 w-4" />
                    Back to all cameras
                </Link>
            </Button>
            <div className="grid lg:grid-cols-2 gap-8">
                <div className="space-y-6">
                    <Card>
                        <CardHeader className="flex-row items-start justify-between">
                             <div className="space-y-2">
                                <Badge variant="secondary">{camera.region}</Badge>
                                <CardTitle className="text-2xl">{camera.name}</CardTitle>
                                <CardDescription>{camera.description}</CardDescription>
                            </div>
                            <FavoriteButton id={camera.id} />
                        </CardHeader>
                    </Card>

                    <div className="aspect-video w-full relative overflow-hidden rounded-lg shadow-lg">
                         <Skeleton className="absolute inset-0" />
                        <Image 
                            src={camera.imageUrl} 
                            alt={`Live feed from ${camera.name}`} 
                            fill
                            className="object-cover transition-opacity duration-500"
                            sizes="(max-width: 1024px) 100vw, 50vw"
                            data-ai-hint={camera.imageHint}
                            priority
                        />
                    </div>
                </div>

                 <div className="h-[300px] lg:h-full w-full rounded-lg overflow-hidden shadow-lg">
                    <Map
                        defaultCenter={{ lat: camera.lat, lng: camera.lng }}
                        defaultZoom={14}
                        gestureHandling={'greedy'}
                        disableDefaultUI={true}
                        mapId={`map-${camera.id}`}
                    >
                        <AdvancedMarker position={{ lat: camera.lat, lng: camera.lng }}>
                             <Pin background={'hsl(var(--primary))'} borderColor={'hsl(var(--primary))'} glyphColor={'hsl(var(--primary-foreground))'} />
                        </AdvancedMarker>
                    </Map>
                </div>
            </div>
        </div>
    );
}
